.home-title{
    font-size: 60px;
    margin: 100px 0 40px;
    text-align: center;
}

.container-card{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    min-width: 100%;
    max-width: 1300px;
    margin: 0 auto 0;
}

.container-card a{
    text-decoration: none;
    color: black;
}

@media screen and (max-width:889px) {
    .container-card{
      margin: 0;
      padding: 0;
    }

    .home-title{
        font-size: 20px;
        /* margin: 100px 0 40px; */
        text-align: center;
    }
}
