.card{
    max-width: 350px;
    min-width: 350px;
    /* padding: 10px; */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    margin: 10px;
    cursor: pointer;
    min-height: 200px;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card h2{
    font-size: 25px;
    margin: 10px;
    border: none;
}

.card p {
    font-size: 18px;
    margin: 0 0 10px 10px;
}

.card img{
    width: 100%;
    max-height: 350px;
    border-radius: 5px;
}


@media screen and (max-width:889px) {
      .card{
        max-width: 150px;
        min-width: 150px;
        /* margin: 5px;
        min-height: 100px; */
      }
}