nav{
    position:fixed;
    top: 0;
    width: 100%;
    height: 70px;
    background-color: black;
}

.liste{
    list-style-type: none;
    background-color: black;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 18px;
}

.items a{
    margin:0 15px;
    color: white;
    cursor: pointer;
    text-decoration: none;
}

.btn{
    display: none;
    position: absolute;
    right: 10px;
    top: 7px;
    padding: 5px;
    color: #000;
    font-size: 18px;
}

@media screen and (max-width:500px) {
    .liste{
        flex-direction: column;
        height: auto;
    }

    .items:nth-child(1){
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        margin-top: 50px;
    }

    .items{
        height: 70px;
        width: 100%;
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        line-height: 70px;
        text-align: center;
    }

    .btn{
        display: block;
    }
}